import React , {Component} from "react";
import Airtable from 'airtable-node'

const Log = new Airtable({apiKey: 'keywMvCl7aRV4a5af'})
    .base('appMcSmdPtPWcBhIX')
    .table('Log')

class Todo extends Component {
    delete = () => {
        var element = document.getElementById(this.props.id);
        element.style.opacity = "0.1";

        Log.delete(this.props.id).then(resp => {
            console.log("Deleting" + this.props.id)
            console.log(resp)
            
            element.style.borderColor = "red";
            element.style.borderWidth = "4px";
        })
    }

    markComplete = () => {
        var element = document.getElementById(this.props.id);
        element.style.opacity = "0.1";
        
        Log.update(this.props.id, {"Concluded": true}).then(resp => {
            console.log(resp)
            console.log("Concluded? " + this.props.concluded)
            
            element.style.borderColor = "green";
            element.style.borderWidth = "4px";
        })
    }

    /* let atHref = "https://airtable.com/tblo6SQZQakNq4URH/viwd4XwZ1wC8esYgg" + this.props.id; */

    openInAt = () => {
        console.log("Opening in Airtable")
        window.open("https://airtable.com/tblJy7BTWYrsbf5M5/viwlybKN4v201IY8V/" + this.props.id)
    }
    
    render() {
        return (
        <div>
            <a href = {this.href} className={this.props.concluded === 1 ? "border-4 border-green-600 shadow-md hover:shadow-lg active:border-gray-500 group block rounded-lg p-3 m-1 text-left justify-left opacity-50" : "shadow-md hover:shadow-lg active:border-gray-500 group block rounded-lg p-3 border hover:border-gray-200 m-1 text-left justify-left"} id={this.props.id}>
                <div className="pb-0">
                    <div className="font-medium text-black text-md">{this.props.name}</div>
                    {typeof this.props.notes !== "undefined" ? (<div className="text-sm text-gray-500">{this.props.notes}</div>) : ""}
                </div>

                <div className="space-y-0">
                    {'goals' in this.props && typeof this.props.goals !== "undefined" ? (
                            this.props.goals
                                .split(",")
                                .map((goal) => (
                                    <div className="inline-block rounded py-1 px-1 bg-gray-300 text-white mr-1" style={{fontSize: "0.4rem"}}>{goal}</div>
                                ))
                            ) : (
                                ""
                    )}
                </div>

                <div className="grid grid-cols-3 space-x-1 mt-1">
                    <button onClick={() => this.markComplete()}
                            className= "rounded-lg px-3 py-1 border active:border-gray-500 text-center focus:outline-none shadow hover:shadow-md active:shadow-sm text-xs"
                        >
                            C
                    </button>

                    <button onClick={() => this.delete()}
                            className= "rounded-lg px-3 py-1 border active:border-gray-500 text-center focus:outline-none shadow hover:shadow-md active:shadow-sm text-xs"
                        >
                            D
                    </button>

                    <button onClick={() => this.openInAt()}
                            className= "rounded-lg px-3 py-1 border active:border-gray-500 text-center focus:outline-none shadow hover:shadow-md active:shadow-sm text-xs"
                        >
                            AT
                    </button>
                </div>
            </a>
                
        </div>
        );
    }
}

export default Todo